body,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  font-family: "Ubuntu", sans-serif;
}
body,
html {
  height: max-content;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  width: 97.3%;
  background-color: black;
  color: white;
  padding: 20px;
  text-align: center;
}

nav span {
  letter-spacing: 5px;
  text-transform: uppercase;
}

.loader {
  display: flex;
  margin: auto;
  margin-top: 130px;
}

.search-c {
  position: relative;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  transition: 1s;
}

.search-c span {
  display: block;
  text-align: center;
  font-size: 30px;
  margin: 30px;
  transition: 1s;
}

.search-c input {
  padding: 15px;
  font-size: 20px;
  border-radius: 20px;
  outline: none;
  width: 100%;
}

.search-c.valid {
  top: 60px;
}

.search-c span.valid {
  display: none;
}

.track-box {
  position: relative;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  padding: 15px;
  font-size: 20px;
  outline: none;
}

.track-box img {
  width: 100px;
  height: 100px;
  border-radius: 5px;
}

.track-box span:nth-child(1) {
  position: absolute;
  top: 30px;
  left: 130px;
  text-decoration: none;
  color: black;
}

.track-box span:nth-child(4) {
  position: absolute;
  top: 80px;
  left: 130px;
  font-size: 15px;
  opacity: 0.7;
}

.trackinfo-c {
  position: relative;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  padding: 15px;
  font-size: 20px;
  margin-bottom: 15px;
}

.trackinfo-c img {
  width: 300px;
  display: block;
  margin: auto;
}

.trackinfo-c button {
  background: none;
  outline: none;
  border: 1px solid black;
  padding: 10px;
  border-radius: 15px;
  cursor: pointer;
  transition: 0.5s-;
}

.trackinfo-c button:hover {
  background-color: black;
  color: white;
}

.trackinfo-c span:nth-child(3) {
  font-weight: bold;
  font-size: 25px;
  margin: 20px;
  display: flex;
  justify-content: center;
}

/*412 x 740*/
@media only screen and (max-width: 600px) {
  body,
  html {
    min-width: 412px;
  }

  .search-c {
    width: 90%;
  }

  .track-box {
    width: 90%;
  }

  .track-box span:nth-child(1) {
    font-size: 14px;
    top: 25px;
  }

  .track-box span:nth-child(4) {
    font-size: 12px;
    top: 85px;
  }
  .trackinfo-c {
    width: 90%;
  }
  .trackinfo-c pre {
    font-size: 15px;
    width: 90%;
    font-family: serif;
    line-height: 1.5;
  }
  .trackinfo-c button {
    margin: 20px 0;
  }
  .loader {
    width: 439px;
  }
}
